
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
  background: linear-gradient(135deg, #0a0a2a, #000033, #000000);
  color: #fff;
  min-height: 100vh;
  overflow-x: hidden;
  
}

.App {
  position: relative;
  min-height: 100vh;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  position: relative;
  z-index: 2;
}

/* Loading Screen */
.loading-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(135deg, #0a0a2a, #000033);
}

.loading-text {
  margin-top: 2rem;
  font-size: 1.2rem;
  color: #4facfe;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 0.7; }
  50% { opacity: 1; }
}

/* Header Styles */
.header {
  text-align: center;
  margin-bottom: 4rem;
  padding-top: 2rem;
  /* position: relative; */
}

.nasa-logo {
  width: 120px;
  margin: 0 auto 1.5rem;
  filter: drop-shadow(0 0 10px rgba(255, 255, 255, 0.3));
  transition: all 0.3s ease;
}

.nasa-logo:hover {
  transform: scale(1.05);
  filter: drop-shadow(0 0 20px rgba(79, 172, 254, 0.6));
}

.nasa-logo.loading {
  animation: float 3s ease-in-out infinite;
}

.title {
  font-size: 3.5rem;
  margin-bottom: 1rem;
  background: linear-gradient(to right, #ffffff, #4facfe, #00f2fe);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  text-shadow: 0 0 20px rgba(79, 172, 254, 0.3);
  letter-spacing: 2px;
  font-weight: 800;
}

.subtitle {
  font-size: 1.3rem;
  color: #a0a0ff;
  margin-bottom: 1.5rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
}

.nasa-badge {
  display: inline-block;
  background: linear-gradient(45deg, #0b3d91, #fc3d21);
  color: #fff;
  padding: 0.8rem 2rem;
  border-radius: 30px;
  font-weight: bold;
  margin-top: 1rem;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(252, 61, 33, 0.4);
  letter-spacing: 1px;
  transition: all 0.3s ease;
}

.nasa-badge:hover {
  transform: translateY(-2px);
  box-shadow: 0 0 30px rgba(252, 61, 33, 0.6);
}

.badge-glow {
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.4), transparent);
  transition: left 0.5s ease;
}

.nasa-badge:hover .badge-glow {
  left: 100%;
}

/* Team Section */
.team-section {
  margin-top: 3rem;
}

.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 3rem;
  justify-items: center;
}

/* Team Member Styles */
.team-member {
  opacity: 0;
  animation: fadeInUp 0.6s ease forwards;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.member-card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 100%;
  max-width: 320px;
  padding: 1.5rem;
  border-radius: 20px;
  background: rgba(16, 16, 48, 0.6);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.4s ease;
  overflow: hidden;
}

.member-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  border-color: rgba(79, 172, 254, 0.3);
}

.member-glow {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at center, rgba(79, 172, 254, 0.1) 0%, transparent 70%);
  opacity: 0;
  transition: opacity 0.4s ease;
}

.member-card:hover .member-glow {
  opacity: 1;
}

.photo-container {
  width: 180px;
  height: 180px;
  border-radius: 50%;
  margin-bottom: 1.5rem;
  position: relative;
  background: linear-gradient(45deg, #ff5e7d, #ffcb47, #4facfe, #00f2fe);
  background-size: 400% 400%;
  animation: gradientBorder 8s ease infinite;
  padding: 4px;
  transition: all 0.4s ease;
}

.member-card:hover .photo-container {
  transform: scale(1.1) rotate(5deg);
  animation-duration: 4s;
}

.photo-skeleton {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: linear-gradient(90deg, #2a2a5a 25%, #3a3a7a 50%, #2a2a5a 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
}

@keyframes loading {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

.member-photo {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  background: #0a0a2a;
  transition: all 0.4s ease;
  opacity: 0;
}

.member-photo.loaded {
  opacity: 1;
}

.photo-glow {
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  background: linear-gradient(45deg, #ff5e7d, #ffcb47, #4facfe, #00f2fe);
  border-radius: 50%;
  z-index: -1;
  opacity: 0;
  filter: blur(15px);
  transition: opacity 0.4s ease;
}

.member-card:hover .photo-glow {
  opacity: 0.7;
}

.social-links {
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 0.5rem;
  opacity: 0;
  transition: all 0.4s ease;
}

.member-card:hover .social-links {
  bottom: 10px;
  opacity: 1;
}

.social-link {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  text-decoration: none;
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.social-link:hover {
  background: rgba(79, 172, 254, 0.8);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(79, 172, 254, 0.4);
}

.member-info {
  position: relative;
  z-index: 2;
}

.member-name {
  font-size: 1.4rem;
  font-weight: bold;
  color: #00f2fe;
  margin-bottom: 0.5rem;
  text-shadow: 0 0 10px rgba(0, 242, 254, 0.7);
  letter-spacing: 1px;
}

.member-role {
  font-size: 1rem;
  color: #a0a0ff;
  background: rgba(16, 16, 48, 0.8);
  padding: 0.5rem 1rem;
  border-radius: 20px;
  border: 1px solid rgba(160, 160, 255, 0.3);
  margin-bottom: 1rem;
  display: inline-block;
}

.member-description {
  font-size: 0.9rem;
  color: #ccccff;
  line-height: 1.5;
  opacity: 0;
  max-height: 0;
  transition: all 0.4s ease;
}

.member-card:hover .member-description {
  opacity: 1;
  max-height: 100px;
}

@keyframes gradientBorder {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Footer */
.footer {
  text-align: center;
  margin-top: 5rem;
  padding: 2rem 0;
  border-top: 1px solid rgba(255,255,255,0.1);
}

.footer p {
  font-size: 1.1rem;
  color: #4facfe;
  margin-bottom: 1rem;
}

.mission-statement {
  font-style: italic;
  color: #a0a0ff;
  max-width: 500px;
  margin: 0 auto;
  line-height: 1.6;
}

/* Stars Background */
.stars {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
}

.star {
  position: absolute;
  background-color: #fff;
  border-radius: 50%;
  animation: twinkle 8s infinite;
}

@keyframes twinkle {
  0%, 100% {
    opacity: 0.2;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.2);
  }
}

/* Floating Elements */
.floating-element {
  position: fixed;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.05);
  box-shadow: 0 0 30px rgba(79, 172, 254, 0.2);
  animation: float 15s infinite linear;
  z-index: 1;
}

@keyframes float {
  0% {
    transform: translateY(0) rotate(0deg);
  }
  50% {
    transform: translateY(-20px) rotate(180deg);
  }
  100% {
    transform: translateY(0) rotate(360deg);
  }
}



/* Responsive Design */
@media (max-width: 1024px) {
  .team-grid {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
  }
}

@media (max-width: 768px) {
  .title {
    font-size: 2.5rem;
  }
  
  .subtitle {
    font-size: 1.1rem;
  }
  
  .team-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.5rem;
  }
  
  .photo-container {
    width: 150px;
    height: 150px;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 1rem;
  }
  
  .title {
    font-size: 2rem;
  }
  
  .team-grid {
    grid-template-columns: 1fr;
  }
  
  .member-card {
    max-width: 280px;
  }
}

.social-link {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  text-decoration: none;
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.2);
  cursor: pointer;
  border: none;
  outline: none;
}

.social-link:hover {
  background: rgba(79, 172, 254, 0.8);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(79, 172, 254, 0.4);
}

.social-link:focus {
  outline: 2px solid #4facfe;
  outline-offset: 2px;
}