.play-icon {
  font-size: 3rem;
  color: #4cc9f0;
  margin-bottom: 1rem;
}

.video-label {
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.video-subtitle {
  color: #b0b0b0;
  font-size: 0.9rem;
}

/* Hide fallback when video is loaded */
.video-player:has(video[src]) .video-fallback {
  display: none;
}

/* Video controls styling */
.demo-video::-webkit-media-controls-panel {
  background: rgba(0, 0, 0, 0.7);
}

.demo-video::-webkit-media-controls-play-button {
  background: #4cc9f0;
  border-radius: 50%;
}

.language-toggle {
  display: flex;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  padding: 4px;
}

.lang-btn {
  flex: 1;
  padding: 0.5rem 1rem;
  border: none;
  background: transparent;
  color: #b0b0b0;
  cursor: pointer;
  border-radius: 6px;
  transition: all 0.3s;
}

.lang-btn.active {
  background: rgba(76, 201, 240, 0.2);
  color: #4cc9f0;
}

/* Sections */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.section-title {
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 3rem;
  font-weight: 700;
}

/* Features */
.features {
  padding: 80px 0;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.feature-card {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  padding: 2rem;
  text-align: center;
  transition: transform 0.3s, box-shadow 0.3s;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.feature-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.feature-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.feature-card h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.feature-card p {
  color: #b0b0b0;
  line-height: 1.6;
}

/* Data Section */
.data-section {
  padding: 80px 0;
  background: rgba(255, 255, 255, 0.02);
}

.data-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
}

.data-text h2 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
}

.data-text p {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #b0b0b0;
  margin-bottom: 2rem;
}

.data-stats {
  list-style: none;
}

.data-stats li {
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.data-stats strong {
  color: #4cc9f0;
}

.data-visual {
  display: flex;
  justify-content: center;
}

.graph-placeholder {
  width: 300px;
  height: 200px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  position: relative;
  overflow: hidden;
  padding: 1rem;
}

.graph-line {
  height: 2px;
  background: rgba(255, 255, 255, 0.1);
  margin-bottom: 1.5rem;
  position: relative;
}

.graph-line::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0;
  background: #4cc9f0;
  animation: graphFill 3s ease-in-out infinite;
}

.graph-line:nth-child(2)::after {
  animation-delay: 0.5s;
}

.graph-line:nth-child(3)::after {
  animation-delay: 1s;
}

@keyframes graphFill {
  0% { width: 0; }
  50% { width: 100%; }
  100% { width: 0; }
}

.transit-indicator {
  position: absolute;
  bottom: 1rem;
  left: 50%;
  width: 4px;
  height: 30px;
  background: #ff6b6b;
  transform: translateX(-50%);
  animation: transitPulse 2s ease-in-out infinite;
}

@keyframes transitPulse {
  0%, 100% { opacity: 0.3; height: 10px; }
  50% { opacity: 1; height: 30px; }
}

/* Footer */
.footer {
  background: rgba(5, 10, 20, 0.9);
  padding: 3rem 0 1rem;
  margin-top: 4rem;
}

.footer-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin-bottom: 2rem;
}

.footer-section h4 {
  margin-bottom: 1rem;
  color: #4cc9f0;
}

.footer-section a {
  display: block;
  color: #b0b0b0;
  text-decoration: none;
  margin-bottom: 0.5rem;
  transition: color 0.3s;
}

.footer-section a:hover {
  color: #4cc9f0;
}

.footer-bottom {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding-top: 1rem;
  text-align: center;
  color: #b0b0b0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .nav-menu {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background: rgba(10, 15, 29, 0.95);
    flex-direction: column;
    padding: 1rem;
    gap: 1rem;
  }
  
  .nav-menu.active {
    display: flex;
  }
  
  .menu-toggle {
    display: flex;
  }
  
  .hero-content {
    grid-template-columns: 1fr;
    gap: 2rem;
    text-align: center;
  }
  
  .hero-title {
    font-size: 2.5rem;
  }
  
  .data-content {
    grid-template-columns: 1fr;
    gap: 2rem;
    text-align: center;
  }
  
  .hero-buttons {
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .video-player {
    height: 250px;
  }
}