
/* Base Styles & CSS Variables */
:root {
    --bg-primary: #0a0a0f;
    --bg-secondary: #0f0f17;
    --bg-tertiary: #1a1a2e;
    --bg-card: rgba(26, 26, 46, 0.8);
    --bg-glass: rgba(15, 15, 23, 0.7);

    --text-primary: #ffffff;
    --text-secondary: #b8bcc8;
    --text-muted: #8892b0;

    --accent-primary: #00f5ff;
    --accent-secondary: #7c3aed;
    --accent-success: #00ff88;
    --accent-warning: #ffaa00;
    --accent-danger: #ff4757;

    --border-primary: rgba(0, 245, 255, 0.2);
    --border-secondary: rgba(255, 255, 255, 0.1);

    --shadow-primary: 0 8px 32px rgba(0, 245, 255, 0.1);
    --shadow-secondary: 0 4px 16px rgba(0, 0, 0, 0.3);

    --gradient-primary: linear-gradient(135deg, #00f5ff 0%, #7c3aed 100%);
    --gradient-secondary: linear-gradient(135deg, #7c3aed 0%, #00f5ff 100%);
    --gradient-bg: radial-gradient(ellipse at top, #0f0f17 0%, #0a0a0f 100%);
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
    background: var(--bg-primary);
    color: var(--text-primary);
    overflow-x: hidden;
}

/* Main Layout */
.stareye-container {
    min-height: 100vh;
    background: var(--gradient-bg);
    position: relative;
}

.stareye-container::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background:
        radial-gradient(circle at 20% 50%, rgba(0, 245, 255, 0.03) 0%, transparent 50%),
        radial-gradient(circle at 80% 20%, rgba(124, 58, 237, 0.03) 0%, transparent 50%),
        radial-gradient(circle at 40% 80%, rgba(0, 255, 136, 0.02) 0%, transparent 50%);
    pointer-events: none;
    z-index: 1;
}

.stareye-container > * {
    position: relative;
    z-index: 2;
}

/* Loading State */
.loading-container {
    min-height: 100vh;
    background: var(--gradient-bg);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}

.loading-text {
    font-size: 1.2rem;
    font-weight: 500;
    background: var(--gradient-primary);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: pulse 2s infinite ease-in-out;
}

.loading-spinner {
    width: 60px;
    height: 60px;
    border: 2px solid rgba(0, 245, 255, 0.2);
    border-top: 2px solid var(--accent-primary);
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

/* Button System */
.btn {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 0.75rem;
    font-weight: 600;
    font-size: 0.95rem;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    overflow: hidden;
    backdrop-filter: blur(10px);
    text-decoration: none;
    
}

.btn::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
    transition: left 0.5s ease;
}

.btn:hover::before {
    left: 100%;
}

.btn-primary {
    background: var(--gradient-primary);
    color: white;
    box-shadow: var(--shadow-primary);
}

.btn-primary:hover {
    transform: translateY(-2px);
    box-shadow: 0 12px 40px rgba(0, 245, 255, 0.2);
}

.btn-secondary {
    background: var(--gradient-secondary);
    color: white;
    box-shadow: 0 8px 32px rgba(124, 58, 237, 0.1);
}

.btn-secondary:hover {
    transform: translateY(-2px);
    box-shadow: 0 12px 40px rgba(124, 58, 237, 0.2);
}

.btn-outline {
    background: var(--bg-glass);
    color: var(--text-primary);
    border: 1px solid var(--border-primary);
    backdrop-filter: blur(20px);
}

.btn-outline:hover {
    background: rgba(0, 245, 255, 0.1);
    border-color: var(--accent-primary);
    transform: translateY(-1px);
}

.btn:disabled {
    opacity: 0.5;
    /* cursor: not-allowed; */
    /* transform: none !important; */
}

.btn:disabled::before {
    display: none;
}

/* Icon System */
.icon {
    width: 1.25rem;
    height: 1.25rem;
    display: inline-block;
    vertical-align: middle;
}

/* Header */
.header {
    padding: 1.5rem 2rem;
    background: var(--bg-glass);
    backdrop-filter: blur(20px);
    border-bottom: 1px solid var(--border-secondary);
    position: sticky;
    top: 0;
    z-index: 100;
}

.header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1400px;
    margin: 0 auto;
}

.header-logo {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.logo-icon {
    width: 2.5rem;
    height: 2.5rem;
    background: var(--gradient-primary);
    border-radius: 0.75rem;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: var(--shadow-primary);
}

.header-title {
    font-size: 1.75rem;
    font-weight: 700;
    background: var(--gradient-primary);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.header-stats {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.stat-badge {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.5rem 1rem;
    background: var(--bg-card);
    border: 1px solid var(--border-primary);
    border-radius: 0.75rem;
    backdrop-filter: blur(10px);
    font-weight: 600;
}

.score-badge {
    color: var(--accent-primary);
    border-color: var(--border-primary);
}

.question-badge {
    color: var(--text-secondary);
    border-color: var(--border-secondary);
}

/* Progress System */
.progress-container {
    padding: 0 2rem 1.5rem;
    max-width: 1400px;
    margin: 0 auto;
}

.progress-track {
    position: relative;
    height: 0.5rem;
    background: var(--bg-tertiary);
    border-radius: 0.25rem;
    overflow: hidden;
    margin-top: 1rem;
}

.progress-fill {
    height: 100%;
    background: var(--gradient-primary);
    border-radius: 0.25rem;
    transition: width 0.8s cubic-bezier(0.4, 0, 0.2, 1);
    position: relative;
}

.progress-fill::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 2rem;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3));
    animation: shimmer 2s infinite ease-in-out;
}

/* Card System */
.card {
    background: var(--bg-card);
    border: 1px solid var(--border-secondary);
    border-radius: 1.25rem;
    backdrop-filter: blur(20px);
    box-shadow: var(--shadow-secondary);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.card:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-primary);
    border-color: var(--border-primary);
}

.card-header {
    padding: 1.5rem;
    border-bottom: 1px solid var(--border-secondary);
}

.card-title {
    font-size: 1.25rem;
    font-weight: 600;
    color: var(--text-primary);
    margin-bottom: 0.25rem;
}

.card-subtitle {
    color: var(--text-muted);
    font-size: 0.875rem;
}

.card-content {
    padding: 1.5rem;
}

/* Main Content Layout */
.main-content {
    padding: 0 2rem 2rem;
    max-width: 1400px;
    margin: 0 auto;
}

.content-grid {
    display: grid;
    grid-template-columns: 1fr 400px;
    gap: 2rem;
    align-items: start;
}

/* Chart Container */
.chart-container {
    position: relative;
}

.chart-canvas-container {
    height: 450px;
    border-radius: 1rem;
    background: var(--bg-secondary);
    border: 1px solid var(--border-secondary);
    position: relative;
    overflow: hidden;
}

.chart-canvas-container::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background:
        radial-gradient(circle at 30% 20%, rgba(0, 245, 255, 0.05) 0%, transparent 50%),
        radial-gradient(circle at 70% 80%, rgba(124, 58, 237, 0.05) 0%, transparent 50%);
    pointer-events: none;
}

.chart-canvas {
    border-radius: 1rem;
    position: relative;
    z-index: 1;
}

/* Question Panel */
.question-panel {
    position: sticky;
    top: 6rem;
}

.question-title {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 0.75rem;
}

.question-description {
    color: var(--text-secondary);
    line-height: 1.6;
    margin-bottom: 2rem;
}

/* Option Buttons */
.options-container {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
    margin-bottom: 2rem;
}

.option-button {
    position: relative;
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 1rem 1.25rem;
    background: var(--bg-glass);
    border: 2px solid var(--border-secondary);
    border-radius: 1rem;
    font-weight: 600;
    font-size: 1rem;
    color: var(--text-primary);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    backdrop-filter: blur(10px);
    overflow: hidden;
}

.option-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.05), transparent);
    transition: left 0.6s ease;
}

.option-button:hover::before {
    left: 100%;
}

.option-icon {
    width: 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.5rem;
    background: var(--bg-secondary);
    border: 1px solid var(--border-secondary);
}

.option-button:hover:not(:disabled) {
    transform: translateY(-1px);
    border-color: var(--border-primary);
    background: rgba(0, 245, 255, 0.05);
}

.option-button.selected {
    border-color: var(--accent-primary);
    background: rgba(0, 245, 255, 0.1);
    box-shadow: 0 0 20px rgba(0, 245, 255, 0.2);
}

.option-button.selected .option-icon {
    background: var(--accent-primary);
    color: var(--bg-primary);
    box-shadow: 0 0 10px rgba(0, 245, 255, 0.3);
}

.option-button:disabled {
    opacity: 0.6;
    /* cursor: not-allowed; */
}

/* Feedback System */
.feedback-container {
    padding: 1.5rem;
    border-radius: 1rem;
    margin-bottom: 1.5rem;
    border: 1px solid;
    position: relative;
    overflow: hidden;
}

.feedback-container::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.1;
    pointer-events: none;
}

.feedback-correct {
    background: rgba(0, 255, 136, 0.1);
    border-color: var(--accent-success);
}

.feedback-correct::before {
    background: var(--accent-success);
}

.feedback-incorrect {
    background: rgba(255, 71, 87, 0.1);
    border-color: var(--accent-danger);
}

.feedback-incorrect::before {
    background: var(--accent-danger);
}

.feedback-header {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    margin-bottom: 0.75rem;
}

.feedback-icon {
    width: 1.5rem;
    height: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.feedback-title {
    font-size: 1.125rem;
    font-weight: 700;
}

.feedback-title.correct {
    color: var(--accent-success);
}

.feedback-title.incorrect {
    color: var(--accent-danger);
}

.feedback-message {
    color: var(--text-secondary);
    line-height: 1.6;
}

/* Details System */
.details-toggle {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.75rem 1rem;
    background: var(--bg-glass);
    border: 1px solid var(--border-secondary);
    border-radius: 0.75rem;
    color: var(--text-secondary);
    font-weight: 500;
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
    margin-bottom: 1rem;
}

.details-toggle:hover {
    background: rgba(0, 245, 255, 0.05);
    border-color: var(--border-primary);
    color: var(--text-primary);
}

.details-arrow {
    transition: transform 0.3s ease;
    width: 1rem;
    height: 1rem;
}

.details-arrow.expanded {
    transform: rotate(180deg);
}

.details-panel {
    background: var(--bg-secondary);
    border: 1px solid var(--border-secondary);
    border-radius: 1rem;
    padding: 1.5rem;
    margin-bottom: 1.5rem;
}

/* System Info Grid */
.system-info-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
    margin-bottom: 1.5rem;
}

.system-info-item {
    padding: 1rem;
    background: var(--bg-glass);
    border: 1px solid var(--border-secondary);
    border-radius: 0.75rem;
    backdrop-filter: blur(10px);
}

.system-info-label {
    font-size: 0.75rem;
    color: var(--text-muted);
    font-weight: 500;
    margin-bottom: 0.5rem;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.system-info-value {
    font-weight: 600;
    font-size: 1rem;
}

.system-info-value.high-confidence {
    color: var(--accent-success);
}

.system-info-value.medium-confidence {
    color: var(--accent-warning);
}

.system-info-value.low-confidence {
    color: var(--accent-danger);
}

.stellar-badge {
    display: inline-flex;
    align-items: center;
    padding: 0.25rem 0.5rem;
    background: rgba(255, 71, 87, 0.2);
    color: var(--accent-danger);
    border-radius: 0.375rem;
    font-size: 0.625rem;
    font-weight: 700;
    margin-left: 0.5rem;
}

/* Diagnostics */
.diagnostics-section {
    margin-top: 1rem;
}

.diagnostics-title {
    font-size: 0.875rem;
    color: var(--text-muted);
    font-weight: 600;
    margin-bottom: 0.75rem;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.diagnostics-grid {
    display: grid;
    gap: 0.5rem;
}

.diagnostic-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.75rem 1rem;
    background: var(--bg-glass);
    border: 1px solid var(--border-secondary);
    border-radius: 0.5rem;
    backdrop-filter: blur(10px);
}

.diagnostic-name {
    font-size: 0.875rem;
    color: var(--text-secondary);
    font-weight: 500;
}

.diagnostic-result {
    display: flex;
    align-items: center;
    gap: 0.25rem;
    font-size: 0.75rem;
    font-weight: 600;
}

.diagnostic-result.pass {
    color: var(--accent-success);
}

.diagnostic-result.fail {
    color: var(--accent-danger);
}

/* Game Complete Screen */
.game-complete-container {
    min-height: 100vh;
    background: var(--gradient-bg);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem;
}

.game-complete-card {
    background: var(--bg-card);
    border: 1px solid var(--border-primary);
    border-radius: 1.5rem;
    padding: 3rem;
    text-align: center;
    max-width: 500px;
    width: 100%;
    backdrop-filter: blur(20px);
    box-shadow: var(--shadow-primary);
}

.game-complete-icon {
    width: 4rem;
    height: 4rem;
    background: var(--gradient-primary);
    border-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto 1.5rem;
    box-shadow: var(--shadow-primary);
}

.game-complete-title {
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 1rem;
    background: var(--gradient-primary);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.final-score {
    font-size: 1.5rem;
    color: var(--accent-primary);
    font-weight: 600;
    margin-bottom: 2rem;
}

.stats-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    margin-bottom: 2rem;
}

.stat-card {
    padding: 1.25rem;
    border-radius: 1rem;
    text-align: center;
}

.stat-card.correct {
    background: rgba(0, 245, 255, 0.1);
    border: 1px solid var(--accent-primary);
}

.stat-card.planets {
    background: rgba(124, 58, 237, 0.1);
    border: 1px solid var(--accent-secondary);
}

.stat-card.accuracy {
    background: rgba(0, 255, 136, 0.1);
    border: 1px solid var(--accent-success);
}

.stat-value {
    font-size: 1.75rem;
    font-weight: 700;
    margin-bottom: 0.25rem;
}

.stat-value.correct {
    color: var(--accent-primary);
}

.stat-value.planets {
    color: var(--accent-secondary);
}

.stat-value.accuracy {
    color: var(--accent-success);
}

.stat-label {
    font-size: 0.875rem;
    color: var(--text-muted);
    font-weight: 500;
}

/* Tips Panel */
.tips-panel {
    padding: 3rem 2rem;
    background: var(--bg-secondary);
    border-top: 1px solid var(--border-secondary);
    margin-top: 3rem;
}

.tips-content {
    max-width: 1400px;
    margin: 0 auto;
}

.tips-title {
    font-size: 1.25rem;
    font-weight: 700;
    margin-bottom: 1.5rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.tips-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 1rem;
}

.tip-card {
    padding: 1.5rem;
    background: var(--bg-card);
    border: 1px solid var(--border-secondary);
    border-radius: 1rem;
    backdrop-filter: blur(10px);
    transition: all 0.3s ease;
}

.tip-card:hover {
    transform: translateY(-2px);
    border-color: var(--border-primary);
    box-shadow: var(--shadow-primary);
}

.tip-icon {
    width: 2.5rem;
    height: 2.5rem;
    background: var(--gradient-primary);
    border-radius: 0.75rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 1rem;
    box-shadow: var(--shadow-primary);
}

.tip-title {
    font-weight: 600;
    margin-bottom: 0.5rem;
}

.tip-description {
    color: var(--text-secondary);
    font-size: 0.875rem;
    line-height: 1.5;
}

/* Cosmic AI Feedback Panel */
.cosmic-ai-panel {
    padding: 2rem 0;
    max-width: 1400px;
    margin: 0 auto;
    position: relative;
}

.ai-hologram-card {
    background: linear-gradient(135deg, 
        rgba(10, 10, 15, 0.95) 0%,
        rgba(26, 26, 46, 0.98) 50%,
        rgba(10, 10, 15, 0.95) 100%);
    border: 1px solid;
    border-image: linear-gradient(45deg, #00f5ff, #7c3aed, #00ff88, #ff6b6b) 1;
    border-radius: 24px;
    padding: 2.5rem;
    position: relative;
    overflow: hidden;
    backdrop-filter: blur(40px);
    box-shadow: 
        0 0 80px rgba(0, 245, 255, 0.15),
        0 0 120px rgba(124, 58, 237, 0.1),
        inset 0 1px 0 rgba(255, 255, 255, 0.1);
    animation: hologramFloat 6s ease-in-out infinite;
}

.hologram-particles {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
}

.particle {
    position: absolute;
    background: var(--accent-primary);
    border-radius: 50%;
    animation: particleFloat 8s infinite linear;
}


.ai-title-glow h3 {
    font-size: 2rem;
    font-weight: 700;
    background: linear-gradient(135deg, #00f5ff, #7c3aed, #00ff88);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-size: 200% 200%;
    animation: gradientShift 3s ease infinite;
    margin: 0 0 0.5rem 0;
}

.ai-title-glow span {
    color: var(--text-muted);
    font-size: 0.9rem;
    display: block;
    margin-bottom: 1rem;
}

.status-pulse {
    display: flex;
    align-items: center;
    gap: 0.75rem;
}

.pulse-ring {
    width: 12px;
    height: 12px;
    background: var(--accent-success);
    border-radius: 50%;
    animation: statusPulse 2s infinite;
}

.status-text {
    color: var(--text-secondary);
    font-size: 0.85rem;
    font-weight: 500;
}

.ai-hologram-message {
    background: linear-gradient(135deg, 
        rgba(15, 15, 23, 0.8) 0%,
        rgba(26, 26, 46, 0.9) 100%);
    border: 1px solid rgba(0, 245, 255, 0.3);
    border-radius: 16px;
    padding: 2rem;
    position: relative;
    overflow: hidden;
    margin-bottom: 1.5rem;
}

.hologram-grid {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: 
        linear-gradient(rgba(0, 245, 255, 0.1) 1px, transparent 1px),
        linear-gradient(90deg, rgba(0, 245, 255, 0.1) 1px, transparent 1px);
    background-size: 20px 20px;
    opacity: 0.3;
    animation: gridMove 20s linear infinite;
}

.message-content {
    position: relative;
    z-index: 2;
}

.cosmic-text {
    color: var(--text-primary);
    font-size: 1.1rem;
    line-height: 1.7;
    margin: 0;
    font-weight: 400;
    text-shadow: 0 0 10px rgba(0, 245, 255, 0.3);
}

.cosmic-text.typing {
    border-right: 3px solid;
    border-image: linear-gradient(45deg, #00f5ff, #7c3aed) 1;
    animation: typewriterGlow 1s infinite;
}



.quantum-typing-indicator {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-top: 1.5rem;
    padding: 1rem;
    background: rgba(0, 245, 255, 0.05);
    border-radius: 12px;
    border: 1px solid rgba(0, 245, 255, 0.2);
}

.quantum-dots {
    display: flex;
    gap: 0.5rem;
}

.quantum-dot {
    width: 8px;
    height: 8px;
    background: linear-gradient(45deg, #00f5ff, #7c3aed);
    border-radius: 50%;
    animation: quantumBounce 1.4s infinite ease-in-out;
}

.quantum-dot:nth-child(1) { animation-delay: -0.32s; }
.quantum-dot:nth-child(2) { animation-delay: -0.16s; }
.quantum-dot:nth-child(3) { animation-delay: 0s; }
.quantum-dot:nth-child(4) { animation-delay: 0.16s; }

.signal-bars {
    display: flex;
    align-items: end;
    gap: 2px;
    height: 20px;
    margin-left: auto;
}

.signal-bars .bar {
    width: 3px;
    background: linear-gradient(to top, #00f5ff, #7c3aed);
    border-radius: 2px;
    animation: signalWave 2s infinite ease-in-out;
}

.signal-bars .bar:nth-child(1) { height: 5px; animation-delay: 0s; }
.signal-bars .bar:nth-child(2) { height: 10px; animation-delay: 0.2s; }
.signal-bars .bar:nth-child(3) { height: 15px; animation-delay: 0.4s; }
.signal-bars .bar:nth-child(4) { height: 10px; animation-delay: 0.6s; }
.signal-bars .bar:nth-child(5) { height: 5px; animation-delay: 0.8s; }

.ai-cosmic-footer {
    border-top: 1px solid rgba(0, 245, 255, 0.2);
    padding-top: 1.5rem;
}

.tech-specs {
    display: flex;
    justify-content: center;
    gap: 2rem;
    flex-wrap: wrap;
}

.tech-specs .spec {
    color: var(--text-muted);
    font-size: 0.8rem;
    width: 50px;
    height: 50px;
    font-weight: 500;
    padding: 0.5rem 1rem;
    background: rgba(0, 245, 255, 0.1);
    
    border-radius: 100%;
    border: 1px solid rgba(0, 245, 255, 0.2);
}

/* Animations */
@keyframes spin {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
}

@keyframes pulse {
    0%, 100% { transform: translate(-50%, -50%) scale(1); opacity: 1; }
    50% { transform: translate(-50%, -50%) scale(1.2); opacity: 0.8; }
}

@keyframes rotate {
    from { transform: translate(-50%, -50%) rotate(0deg); }
    to { transform: translate(-50%, -50%) rotate(360deg); }
}

@keyframes orbit {
    from { transform: rotate(0deg) translateX(15px) rotate(0deg); opacity: 1; }
    to { transform: rotate(360deg) translateX(15px) rotate(-360deg); opacity: 0.3; }
}

@keyframes shimmer {
    0%, 100% { opacity: 0; }
    50% { opacity: 1; }
}

@keyframes hologramFloat {
    0%, 100% { transform: translateY(0px) rotateX(0.5deg); }
    50% { transform: translateY(-10px) rotateX(-0.5deg); }
}

@keyframes particleFloat {
    0% { transform: translateY(0) rotate(0deg); opacity: 0; }
    10% { opacity: 1; }
    90% { opacity: 1; }
    100% { transform: translateY(-100px) rotate(360deg); opacity: 0; }
}

@keyframes neonPulse {
    0%, 100% { transform: scale(1); box-shadow: 0 0 40px rgba(0, 245, 255, 0.4); }
    50% { transform: scale(1.05); box-shadow: 0 0 60px rgba(0, 245, 255, 0.6); }
}

@keyframes orbitSpin {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
}

@keyframes statusPulse {
    0% { box-shadow: 0 0 0 0 rgba(0, 255, 136, 0.7); }
    70% { box-shadow: 0 0 0 10px rgba(0, 255, 136, 0); }
    100% { box-shadow: 0 0 0 0 rgba(0, 255, 136, 0); }
}

@keyframes gradientShift {
    0% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
    100% { background-position: 0% 50%; }
}

@keyframes gridMove {
    0% { transform: translate(0, 0); }
    100% { transform: translate(20px, 20px); }
}

@keyframes typewriterGlow {
    0%, 100% { border-image-source: linear-gradient(45deg, #00f5ff, #7c3aed); }
    50% { border-image-source: linear-gradient(45deg, #7c3aed, #00ff88); }
}



@keyframes quantumBounce {
    0%, 80%, 100% { transform: scale(0.8); opacity: 0.5; }
    40% { transform: scale(1.2); opacity: 1; }
}

@keyframes signalWave {
    0%, 100% { height: 5px; }
    50% { height: 15px; }
}

/* Responsive Design */
@media (max-width: 1024px) {
    .content-grid {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }

    .question-panel {
        position: static;
    }

    .header-content {
        flex-direction: column;
        gap: 1rem;
        align-items: flex-start;
    }
}

@media (max-width: 768px) {
    .header {
        padding: 1rem;
    }

    .main-content {
        padding: 0 1rem 1rem;
    }

    .progress-container {
        padding: 0 1rem 1rem;
    }

    .system-info-grid {
        grid-template-columns: 1fr;
    }

    .stats-grid {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .game-complete-card {
        padding: 2rem;
    }

    .tips-panel {
        padding: 2rem 1rem;
    }

    .cosmic-ai-panel {
        padding: 1rem 0;
    }
    
    .ai-hologram-card {
        padding: 1.5rem;
        border-radius: 20px;
    }
    
    .ai-cosmic-header {
        flex-direction: column;
        text-align: center;
        gap: 1rem;
    }
    
    .ai-neon-avatar {
        width: 60px;
        height: 60px;
    }
    
    .ai-title-glow h3 {
        font-size: 1.5rem;
    }
    
    .tech-specs {
        gap: 0.5rem;
    }
    
    .tech-specs .spec {
        font-size: 0.7rem;
        padding: 0.4rem 0.8rem;
    }
    
    .quantum-typing-indicator {
        flex-direction: column;
        gap: 0.75rem;
        text-align: center;
    }
    
    .signal-bars {
        margin-left: 0;
    }
}

@media (max-width: 640px) {
    .header-stats {
        flex-direction: column;
        gap: 0.5rem;
        width: 100%;
    }

    .card-content,
    .card-header {
        padding: 1rem;
    }

    .btn {
        padding: 0.75rem 1rem;
        font-size: 0.875rem;
    }
}


