* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  cursor: none;
}

body {
  background: linear-gradient(135deg, #0a0a2a, #000033, #000000);
  color: #fff;
  min-height: 100vh;
  overflow-x: hidden;
}

.leaderboard-app {
  position: relative;
  min-height: 100vh;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 2rem;
  position: relative;
  z-index: 2;
}

/* Header Styles */
header {
  text-align: center;
  margin-bottom: 3rem;
  padding-top: 2rem;
  position: relative;
}

.nasa-logo {
  width: 120px;
  margin: 0 auto 1.5rem;
  filter: drop-shadow(0 0 10px rgba(255, 255, 255, 0.3));
}

.nasa-logo svg {
  width: 100%;
  height: auto;
}

h1 {
  font-size: 3.5rem;
  margin-bottom: 1rem;
  background: linear-gradient(to right, #ffffff, #4facfe, #00f2fe);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  text-shadow: 0 0 20px rgba(79, 172, 254, 0.3);
  letter-spacing: 2px;
  font-weight: 800;
}

.subtitle {
  font-size: 1.3rem;
  color: #a0a0ff;
  margin-bottom: 1.5rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
}

.nasa-badge {
  display: inline-block;
  background: linear-gradient(45deg, #0b3d91, #fc3d21);
  color: #fff;
  padding: 0.8rem 2rem;
  border-radius: 30px;
  font-weight: bold;
  margin-top: 1rem;
  box-shadow: 0 0 20px rgba(252, 61, 33, 0.4);
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 0.9rem;
}

/* Leaderboard Container */
.leaderboard-container {
  background: rgba(16, 16, 48, 0.85);
  border-radius: 20px;
  padding: 2.5rem;
  margin-top: 2rem;
  border: 1px solid rgba(160, 160, 255, 0.2);
  box-shadow: 
    0 0 40px rgba(79, 172, 254, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(15px);
  position: relative;
  overflow: hidden;
}

.leaderboard-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, #4facfe, transparent);
}

.leaderboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid rgba(160, 160, 255, 0.3);
}

.leaderboard-title {
  font-size: 2.2rem;
  background: linear-gradient(to right, #4facfe, #00f2fe);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-weight: 700;
}

.refresh-info {
  display: flex;
  align-items: center;
  gap: 12px;
  color: #a0a0ff;
  font-size: 1rem;
  background: rgba(0, 242, 254, 0.1);
  padding: 0.5rem 1rem;
  border-radius: 20px;
  border: 1px solid rgba(0, 242, 254, 0.2);
}

.refresh-indicator {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #00f2fe;
  animation: pulse 2s infinite;
  box-shadow: 0 0 10px #00f2fe;
}

@keyframes pulse {
  0% { opacity: 0.3; transform: scale(0.8); }
  50% { opacity: 1; transform: scale(1.2); }
  100% { opacity: 0.3; transform: scale(0.8); }
}

/* Top Three Podium */
.top-three {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 2rem;
  margin-bottom: 3rem;
  align-items: end;
}

.podium-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 1.5rem;
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.05);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.podium-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #4facfe, #00f2fe);
}

.podium-item.first-place {
  transform: translateY(-20px);
  background: linear-gradient(135deg, rgba(255, 215, 0, 0.1), rgba(255, 165, 0, 0.05));
  box-shadow: 0 10px 30px rgba(255, 215, 0, 0.3);
}

.podium-rank {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 1.8rem;
  margin-bottom: 1rem;
  box-shadow: 0 0 25px currentColor;
  position: relative;
  z-index: 2;
}

.rank-1 {
  background: linear-gradient(45deg, #FFD700, #FFA500);
  color: #000;
}

.rank-2 {
  background: linear-gradient(45deg, #C0C0C0, #A0A0A0);
  color: #000;
}

.rank-3 {
  background: linear-gradient(45deg, #CD7F32, #8B4513);
  color: #fff;
}

.podium-team {
  font-weight: 700;
  margin-bottom: 0.5rem;
  font-size: 1.3rem;
  color: #fff;
}

.podium-score {
  color: #00f2fe;
  font-weight: bold;
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.podium-project {
  color: #a0a0ff;
  font-size: 0.9rem;
  opacity: 0.8;
}

/* Table Styles */
.table-container {
  overflow-x: auto;
  border-radius: 12px;
  background: rgba(0, 0, 0, 0.3);
}

.leaderboard-table {
  width: 100%;
  border-collapse: collapse;
  min-width: 800px;
}

.leaderboard-table th {
  text-align: left;
  padding: 1.2rem 1.5rem;
  color: #00f2fe;
  font-weight: 600;
  border-bottom: 2px solid rgba(160, 160, 255, 0.3);
  background: rgba(0, 242, 254, 0.05);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 0.9rem;
}

.leaderboard-table td {
  padding: 1.2rem 1.5rem;
  border-bottom: 1px solid rgba(160, 160, 255, 0.1);
  transition: all 0.3s ease;
}

.leaderboard-table tr {
  transition: all 0.3s ease;
  position: relative;
}

.leaderboard-table tr:hover {
  background: rgba(79, 172, 254, 0.15);
  transform: translateX(5px);
}

.leaderboard-table tr::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 3px;
  background: var(--rank-color, #4facfe);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.leaderboard-table tr:hover::before {
  opacity: 1;
}

.rank {
  text-align: center;
  width: 80px;
}

.rank-badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: rgba(79, 172, 254, 0.2);
  border-radius: 50%;
  font-weight: bold;
  font-size: 1.1rem;
  border: 2px solid var(--rank-color, #4facfe);
  color: var(--rank-color, #4facfe);
}

.team-name {
  font-weight: 600;
}

.team-info {
  display: flex;
  align-items: center;
  gap: 12px;
}

.team-name-text {
  font-size: 1.1rem;
}

.project {
  color: #a0a0ff;
}

.score {
  text-align: right;
  font-weight: bold;
  color: #00f2fe;
}

.score-value {
  font-size: 1.2rem;
}

.progress-cell {
  width: 200px;
}

.progress-bar {
  width: 100%;
  height: 8px;
  background: rgba(160, 160, 255, 0.2);
  border-radius: 4px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(to right, #4facfe, #00f2fe);
  border-radius: 4px;
  transition: width 0.8s ease;
  box-shadow: 0 0 10px rgba(79, 172, 254, 0.5);
}

/* Stats Footer */
.stats-footer {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
  margin-top: 2rem;
  padding: 2rem;
  background: rgba(16, 16, 48, 0.7);
  border-radius: 15px;
  border: 1px solid rgba(160, 160, 255, 0.2);
}

.stat-item {
  text-align: center;
  padding: 1rem;
}

.stat-value {
  font-size: 2rem;
  font-weight: bold;
  color: #00f2fe;
  margin-bottom: 0.5rem;
}

.stat-label {
  color: #a0a0ff;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Cursor Styles */
.cursor {
  position: fixed;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  pointer-events: none;
  z-index: 9999;
  mix-blend-mode: difference;
  transition: transform 0.15s ease;
  background: radial-gradient(circle, #ff5e7d, #ffcb47, #4facfe);
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.7);
}

.cursor-follower {
  position: fixed;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  pointer-events: none;
  z-index: 9998;
  mix-blend-mode: difference;
  transition: transform 0.3s ease;
  border: 2px solid rgba(255, 255, 255, 0.8);
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
}

.cursor-trail {
  position: fixed;
  border-radius: 50%;
  pointer-events: none;
  z-index: 9997;
  mix-blend-mode: difference;
  background: linear-gradient(45deg, #ff5e7d, #ffcb47, #4facfe);
  animation: trailFade 0.5s ease-out forwards;
}

@keyframes trailFade {
  to {
    opacity: 0;
    transform: scale(0.5);
  }
}

/* Stars Background */
.stars {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
}

.star {
  position: absolute;
  background-color: #fff;
  border-radius: 50%;
  animation: twinkle 8s infinite;
}

@keyframes twinkle {
  0%, 100% {
    opacity: 0.2;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.2);
  }
}

/* Floating elements */
.floating-element {
  position: fixed;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.05);
  box-shadow: 0 0 30px rgba(79, 172, 254, 0.2);
  animation: float 15s infinite linear;
  z-index: 1;
}

@keyframes float {
  0% {
    transform: translateY(0) rotate(0deg);
  }
  50% {
    transform: translateY(-20px) rotate(180deg);
  }
  100% {
    transform: translateY(0) rotate(360deg);
  }
}

/* Update animation */
@keyframes highlightUpdate {
  0% { 
    background-color: rgba(79, 172, 254, 0.3);
    box-shadow: 0 0 20px rgba(79, 172, 254, 0.5);
  }
  100% { 
    background-color: transparent;
    box-shadow: none;
  }
}

.updated {
  animation: highlightUpdate 2s ease;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .container {
    max-width: 100%;
    padding: 1.5rem;
  }
  
  .top-three {
    gap: 1rem;
  }
  
  .podium-item {
    padding: 1rem;
  }
}

@media (max-width: 768px) {
  .top-three {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .podium-item.first-place {
    transform: none;
    order: -1;
  }
  
  h1 {
    font-size: 2.5rem;
  }
  
  .leaderboard-title {
    font-size: 1.8rem;
  }
  
  .leaderboard-header {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
  
  .stats-footer {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .leaderboard-container {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 1rem;
  }
  
  h1 {
    font-size: 2rem;
  }
  
  .subtitle {
    font-size: 1.1rem;
  }
  
  .leaderboard-table th, 
  .leaderboard-table td {
    padding: 0.8rem 0.5rem;
  }
  
  .stats-footer {
    grid-template-columns: 1fr;
  }
  
  .podium-rank {
    width: 50px;
    height: 50px;
    font-size: 1.4rem;
  }
}

/* Scrollbar Styling */
.table-container::-webkit-scrollbar {
  height: 8px;
}

.table-container::-webkit-scrollbar-track {
  background: rgba(160, 160, 255, 0.1);
  border-radius: 4px;
}

.table-container::-webkit-scrollbar-thumb {
  background: linear-gradient(to right, #4facfe, #00f2fe);
  border-radius: 4px;
}

.table-container::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(to right, #00f2fe, #4facfe);
}